
.fileInput
{
  border-bottom: 4px solid lightgray;
  border-right: 4px solid lightgray;
  border-top: 1px solid black;
  border-left: 1px solid black;
  padding: 10px;
  margin: 15px;
  cursor: pointers;
  }

.imgPreview{
  margin: auto;
  text-align: center;
  height: 300px;
  width: 500px;
  border-radius: 5px;
  border-left: 5px solid gray;
  border-right: 5px solid gray;
  border-top: 5px solid gray;
  border-bottom: 5px solid gray;
}

  img{
    width: 100%;
    height: 100%;
  }

.previewText{
  width: 100%;
  margin-top: 20px;
}
  
.submitButton
{
  padding: 12px;
  margin-left: 10px;
  background: white;
  border: 4px solid lightgray;
  border-radius: 15px;
  font-weight: 700;
  font-size: 10pt;
  cursor: pointer;
}
 &:hover{
    background: #efefef;
 }

.centerText
{
  text-align: center;
  width: 500px;
}
.progress {
  margin:20px auto;
  padding:0;
  width:20%;
  height:30px;
  overflow:hidden;
  background:#e5e5e5;
  border-radius:6px;
}

.bar {
  position:relative;
  float:left;
  min-width:1%;
  height:100%;
  background:cornflowerblue;
}

.percent {
  position:absolute;
  top:50%;
  left:50%;
  transform:translate(-50%,-50%);
  margin:0;
  font-family:tahoma,arial,helvetica;
  font-size:12px;
  color:white;
}